#awards {
	/*background-image: linear-gradient(to top, #112233, #1b2b3d, #253547, #303f52, #3a495d);*/
	padding: 3% 0%;
}

.award-list {
	list-style-type: none;
	display: -ms-flexbox;
	display: flex;
	-ms-flex-pack: distribute;
	    justify-content: space-around;
	-ms-flex-align: center;
	    align-items: center;
	-ms-flex-wrap: wrap;
	    flex-wrap: wrap;
}

.award-list .fa {
	color: #fff;
	font-size: 2em;
}
.award-item {
	-ms-flex: 1 1;
	    flex: 1 1;
	min-width: 25%;
	color: #fff;
	display: -ms-flexbox;
	display: flex;
	-ms-flex-align: start;
	    align-items: flex-start;
	-ms-flex-pack: start;
	    justify-content: flex-start;
	font-family: 'Lato', 'serif';
}
.trophy {
	-ms-flex: 2 1;
	    flex: 2 1;
	text-align: center;
	display: -ms-flexbox;
	display: flex;
	-ms-flex-direction: column;
	    flex-direction: column;
	-ms-flex-align: center;
	    align-items: center;
	-ms-flex-pack: start;
	    justify-content: flex-start;
}
.where {
	-ms-flex: 4 1;
	    flex: 4 1;
	display: -ms-flexbox;
	display: flex;
	padding-left: 3%;
	-ms-flex-direction: column;
	    flex-direction: column;
	-ms-flex-align: start;
	    align-items: flex-start;
	-ms-flex-pack: start;
	    justify-content: flex-start;
}

.award-title {
	margin: 0;
	font-size: 1em;
}
.award-position {
	margin: 0;
	font-size: 1em;
	font-weight: lighter;
}
.award-time {
	margin: 0;
	font-size: 1em;
	font-weight: lighter;
}

@media screen and (max-width: 600px) {
	.award-list {
		-ms-flex-direction: column;
		    flex-direction: column;
		-ms-flex-wrap: nowrap;
		    flex-wrap: nowrap;
	}
	.award-item {
		padding: 2% 0%;
		min-width: 80%;
	}
	.trophy {
	}
	.award-position {
		font-size: 0.8em;
	}
	.award-title {
		font-size: 0.8em;
	}
	.award-time {
		font-size: 0.8em;
	}
}



