.header {
	display: -ms-flexbox;
	display: flex;
	position: fixed;
	z-index: 10;
	-ms-flex-pack: distribute;
	    justify-content: space-around;
	-ms-flex-wrap: wrap;
	    flex-wrap: wrap;
	-ms-flex-align: center;
	    align-items: center;
	/*max-height: 30vh;*/
	width: 100%;
}
.logo {
	font-size: 1.5em;
	color: #fff;
}
.logo-link {
	text-decoration: none;
}

@media screen and (max-width: 850px) and (min-width: 520px) {
	.header {
		position: static;
		max-height: 60vh;
	}
}



