#contact {
	/*background-image: linear-gradient(to bottom, #112233, #1b2b3d, #253547, #303f52, #3a495d);*/
}
.contact-list {
	list-style-type: none;
	display: -ms-flexbox;
	display: flex;
	padding: 1% 30%;
	-ms-flex-pack: distribute;
	    justify-content: space-around;
}

.contact-list-item .fa {
	color: #fff;
	font-size: 3em;
}

.contact-list-item:hover {
	-webkit-transform: rotateY(0deg);
	        transform: rotateY(0deg);
	-webkit-animation: rotates 800ms ease-in-out 10ms forwards;
	        animation: rotates 800ms ease-in-out 10ms forwards;
}

@media screen and (max-width: 600px) {
	.contact-list-item .fa {
		font-size: 1.5em;
	}
	.contact-list {
		padding: 1% 1%;
	}
}

@-webkit-keyframes rotates {
	from {
		-webkit-transform: rotateY(0deg);
		        transform: rotateY(0deg);
	}
	to {
		-webkit-transform: rotateY(360deg);
		        transform: rotateY(360deg);
	}
}

@keyframes rotates {
	from {
		-webkit-transform: rotateY(0deg);
		        transform: rotateY(0deg);
	}
	to {
		-webkit-transform: rotateY(360deg);
		        transform: rotateY(360deg);
	}
}



