.card {
	display: -ms-flexbox;
	display: flex;
	-ms-flex-direction: column;
	    flex-direction: column;
	-ms-flex-wrap: wrap;
	    flex-wrap: wrap;
	-ms-flex-pack: start;
	    justify-content: flex-start;
	-ms-flex-align: center;
	    align-items: center;
	-ms-flex: 1 1;
	    flex: 1 1;
	max-width: 14.5%;
	margin-bottom: 2%;
	background-color: #fff;
	border: 1px solid #fff;
	-webkit-box-shadow: 4px 4px #aaa;
	        box-shadow: 4px 4px #aaa;
}
.card-title {
	font-size: 1.3em;
	color: #123;
	text-align: center;
}
.card-subtitle {
	font-size: 1em;
	color: #444;
	text-align: center;
	font-weight: bold;
}

@media screen and (max-width: 1218px) {
	.card {
		min-width: 25vw;
	}
}

@media screen and (max-width: 600px) {
	.card {
		min-width: 85vw;
		margin-bottom: 5vw;
	}
}



